/**
 * select  ele 
 */

import baseMixin from '../../../mixin/base'

export default {
  name: 'select-ele',

  template: `
    <div :class="[cPrefix]">
      <slot></slot>
    </div>
  `,

  mixins: [baseMixin],

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-select-ele`
    }
  }
}



